  font-weight: 300;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  color: #3d4e61;
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

.search {
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  margin: 20px 8px;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search:has(.search__input:focus) {
  background: linear-gradient(#fff, #f6f6f7 100%);
  font-weight: 900;
}

.search--big {
  gap: 18px;
  height: 70px;
  padding: 24px 24px 22px 25px;
}

.search--small {
  gap: 10px;
  height: 42px;
  padding: 11px 11px 9px 12px;
}

.search__icon {
  background-image: url("Search.ccd3c07b.svg");
  background-position: center;
  background-size: contain;
  display: flex;
}

.search--big .search__icon {
  width: 19px;
  height: 19px;
}

.search--small .search__icon {
  width: 11px;
  height: 11px;
  margin-top: 3px;
}

.search__input {
  background: none;
  border: none;
  outline: none;
  margin: -2px 0 0;
  font-family: Avenir, sans-serif;
}

.search__input:focus::placeholder {
  font-weight: 900;
}

.search--big .search__input {
  font-size: 16px;
  line-height: 22px;
}

.search--small .search__input {
  font-size: 14px;
